}

#coursesTitle {
  color: var(--font-color);
  font-family: var(--font-typeface);
  font-size: var(--font-size);
  font-weight: bold;
}

#footer {
  height: 30px;
  width: 100%;

  background-color: var(--detail-light);
}

#sidebarContainer {
  width: 20%;
  margin-right: 32px;
  margin-left: 108px;
}

#sidebar {
  padding-top: 24px;

  width: 100%;
  height: 100%;
  background-color:var(--detail-light);
}

#coursesHeader {
  color: var(--font-color);
  font-size: 1.6rem;
  font-family: var(--font-typeface);
  font-weight: bold;

  padding: 8px 14px 8px 14px;
  border-top: solid 1.5px var(--background-light);
  border-bottom: solid 1.5px var(--background-light);
  text-align: center;
}

.courseLabel {
  padding-top: 4px;
  padding-bottom: 4px;
  padding-left: 24px;
}

.courseLabel[type="button"] {
  font-size: 1.4rem;
  -webkit-appearance: none;
    vertical-align: middle;
    text-align: left;
    border: 0;
    -webkit-transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
    transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0 0 0 18px;
}

#sidebar li .qButton {
  text-align: left;
}

#sidebar li > .qButton:hover {
  background-color: var(--detail-light);
}

#sidebar .no-results {
  font-family: var(--font-family);
  font-weight: bold;
  color: var(--font-color);
}

/**
 * Progress bar classes, colours and text positioning.
**/

.progressBar * {
  color: var(--font-color);
  font-family: var(--font-family);
  font-weight: bold;
}

.progressBar {
  position: relative;

  width: 300px;
  border: solid 2px var(--detail-light);
}

.progressFill {
  position: relative;
  text-align: left;
  height: 30px;
  line-height: 30px;
  overflow: hidden;
}

.progressBarText {
  position: absolute;
  line-height: 30px;

  top: 0px;
  left: 12px;
}

.progressBarPercent {
  position: absolute;
  line-height: 24px;
  right: 8px;
  top: 4px;
}

.textOverlay {
  color: var(--background-light);

  z-index: 2;
  white-space: nowrap;
}

/**
 * Pie chart colours and element positioning.
**/

.pieChart {
  display: inline-block;
  box-shadow: 0 0 8px 4px var(--background-medium);

  padding: 12px 32px 16px 32px;
  margin: 0px 24px 24px 0px;
}

.ct-chart {
   display: flex;
   position: relative;
   flex-direction: row-reverse;
   max-height: 150px;
   /* max-width: calc(100% - 800px); */

   justify-content: left;
   /* position: relative; */
   /* left: -800px; */
 }

 .pieChart h5 {
   color: var(--background-dark);
   font-weight: bold;
 }

 .ct-chart-pie {
   position: relative;

   max-width: 150px;
   max-height: 150px;
   overflow: hidden;
 }

 .ct-chart-pie g:last-child {
   display: none;
 }

 .ct-slice-pie {
   stroke: var(--background-light);
   stroke-width: 1px;
 }

 .ct-legend {
   display: flex;
   padding-top: 14px;

   flex-direction: column;
   flex-wrap: wrap;
   max-height: 100%;
 }

 .ct-legend li {
   font-family: var(--font-family);
   color: #444;
   line-height: 24px;

   position: relative;
   padding-left: 16px;
   margin-right: 16px;
 }

 .ct-legend li:before {
   width: 12px;
   height: 12px;
   position: absolute;
   left: 0px;
   top: 25%;

   content: '';
   border-radius: 2px;
   border: 1px solid var(--background-dark);
 }

 .ct-series, .ct-legend li:before {
   fill: var(--stat-elem-colour);
   background-color: var(--stat-elem-colour);
 }

 .ct-series-a, .ct-series-0 {
   --stat-elem-colour: darkred;
 }

 .ct-series-b, .ct-series-1 {
   --stat-elem-colour: crimson;
 }

 .ct-series-c, .ct-series-2 {
   --stat-elem-colour: salmon;
 }

 .ct-series-d, .ct-series-3 {
   --stat-elem-colour: tomato;
 }

 .ct-series-e, .ct-series-4 {
   --stat-elem-colour: orangered;
 }

 .ct-series-f, .ct-series-5 {
   --stat-elem-colour: darkorange;
 }

 .ct-series-g, .ct-series-6 {
   --stat-elem-colour: gold;
 }

 .ct-series-h, .ct-series-7 {
   --stat-elem-colour: yellowgreen;
 }

 .ct-series-i, .ct-series-8 {
   --stat-elem-colour: lawngreen;
 }

 .ct-series-j, .ct-series-9 {
   --stat-elem-colour: springgreen;
 }

 .ct-series-k, .ct-series-10 {
   --stat-elem-colour: mediumspringgreen;
 }

 .ct-series-l, .ct-series-11 {
   --stat-elem-colour: lightseagreen;
 }

 .correctAnswer {
   --stat-elem-colour: limegreen;
 }


/**
 * Attendance Tables
**/

.displayTable {
  display: inline-block;
  border: 2px solid var(--detail-light);
  padding: 1px;
}

.displayTable * {
  text-shadow: 0 0 6px var(--font-border);
}

.displayTable .tableTitle {
  text-align: center;
  font-family: var(--font-family);
  font-weight: bold;
  font-size: 1.2rem;
  color: var(--font-color);

  margin: 2px 2px 0 2px;
  padding: 4px;
  background-color: var(--background-medium);
}

.displayTable table {
  display: inline-table;
  border-collapse: separate;
  border-spacing: 2px;
}

.displayTable th, .displayTable td {
  text-align: center;
  font-family: var(--font-family);
  font-weight: bold;
  color: var(--font-color);

  background-color: var(--background-medium);
}

.displayTable td.target {
  background-color: lightcoral;
}

.displayTable td, .displayTable th {
  height: 28px;
}

.displayTable td div, .displayTable th div,
.displayTable td a, .displayTable th a {
  display: block;
  width: 100%;
  height: 100%;

  padding: 4px 8px 4px 8px;
  color: var(--font-color);
}

.displayTable td a, .displayTable th a {
  text-decoration: underline;
  text-underline-position: under;
}

.displayTable td a:hover, .displayTable th a:hover {
  background-color: var(--background-dark);
}

